.sudoku table {
    /* border-spacing: 0; */
    border-collapse: collapse;
    border: 2px solid #000;
}
.sudoku {
    width: 900px;
    height: 900px;
}
.sudoku tr:nth-child(3n):not(:last-child) {
    border-bottom:2px solid #000;
}
.sudoku td:nth-child(3n):not(:last-child) {
    border-right:2px solid #000;
}
.sudoku input {
    position: sticky;
    width: 100px;
    height: 100px;
    font-size: 100px;
    text-align: center;
    margin: 0;
    padding: 0;
    outline: none;
    border: 1px solid #000;
    caret-color: transparent;
    transition: 0.1s;
}
.sudoku input:focus {
    border-radius:0;
    background-color: steelblue;
}
.sudoku input:disabled {
    background-color: silver;
}