* {
    margin: 0;
    padding: 0;
    /* font-family: 'Alumni Sans Pinstripe', sans-serif; */
    /* font-family: 'Nunito', sans-serif; */
    font-family: 'Open Sans', sans-serif;
}

:root {
    --text-main: #fff;
}

body {
    background:url(https://w-dog.ru/wallpapers/3/2/340209901543165.jpg);
    color: var(--text-main);
    background-position: center center;
}
body::after {
    content:'';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.7);
    z-index: -1;
}

h1 {
    font-size: 140px;
    font-family: 'Nunito', sans-serif;
}

h2 {
    font-family: 'Nunito', sans-serif;
}

h3 {

}

h4 {

}

a {

}

button {
    background-color: transparent;
    color: var(--text-main);
    border: none;
    outline: none;
    text-shadow: 0 0 3px #fff;
    cursor: pointer;
    transition: 0.3s;
}

button:hover {
    text-shadow: 0 0 20px #fff,0 0 20px #fff, 0 0 20px #fff, 0 0 5px blueviolet;
}

.pages {
    position: relative;
    height: 100vh;
    width: 100%;
}
.page {
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.5);
    transform: translate(-100%);
    transition: 1s;
}
.page.current {
    transform: translate(0);
}

header {
    height: 100%;
}

header .container {
    height: 100%;
}

.header__main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    position: relative;
}

.header__title{

}

.header__btns {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
}

.header__btns button {
    position: absolute;
    font-size: 50px;
    font-family: 'Alumni Sans Pinstripe', sans-serif;
}

#headerWorks {
    top: 200px;
    right: 200px;
}

#headerSkills {
    bottom: 200px;
    right: 200px;
}

#headerContacts {
    top: 200px;
    left: 200px;
}

#headerSudoku {
    bottom: 200px;
    left: 200px;
}